.popup__container {
  max-width: 430px;
  width: 100%;
  position: relative;
  background: #FFF;    
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px 36px;
  box-sizing: border-box;
}

@media screen and (max-width: 520px) {
  .popup__container  {
    max-width: 240px;
    padding: 30px 20px;    
  }
}